:root {
  --planner-timeline-background-color: black;
  --planner-timeline-background-color-odd: black;
  : black;
  --planner-timeline-border-color: blue;
}

tr[role=row]:nth-child(even),.fc-scrollgrid-sync-table tbody tr:nth-child(even) {
  background-color: var(--planner-timeline-background-color);
}
tr[role=row]:nth-child(odd),.fc-scrollgrid-sync-table tbody tr:nth-child(odd) {
  background-color: var(--planner-timeline-background-color-odd);
}
th[role=columnheader] .fc-datagrid-cell-frame,.fc-timeline-header .fc-day .fc-timeline-slot-frame{ 
  background-color: var(--planner-timeline-background-color-header);
  border-top: 1px solid var(--planner-timeline-border-color);;
  border-bottom: 1px solid var(--planner-timeline-border-color);;
}
.fc-timeline-header .fc-slot .fc-timeline-slot-frame {
  background-color: var(--planner-timeline-background-color-header);
  border-top: 1px solid var(--planner-timeline-border-color);
  border-bottom: 1px solid var(--planner-timeline-border-color);;
}